@import url("https://fonts.googleapis.com/css2?family=Fjalla+One&family=Roboto:wght@300&display=swap");

img {
  min-width: 515px;
  border-radius: 10px /10px;
}

img.fadeout {
  opacity: 0;
}

body {
  /* background-color: #273f70; */
  background-image: url("https://i.ibb.co/VNVLg93/SL-072620-33020-18.jpg");
  background-size: cover;
}

h3 {
  position: absolute;
  margin-left: 215px;
  margin-top: -20px;
}

.main {
  margin-top: 50px;
}

.card {
  width: 535px;
  height: 720px;
  margin: 0 auto;
  background-color: #f8fbfe;
  border-radius: 8px;
  z-index: 1;
  font-family: "Roboto", sans-serif;
  color: #1d1d1d;
}

.tools {
  display: flex;
  align-items: center;
  padding: 9px;
}

.circle {
  padding: 0 4px;
}

.box {
  display: inline-block;
  align-items: center;
  width: 10px;
  height: 10px;
  padding: 1px;
  border-radius: 50%;
}

.red {
  background-color: #ff605c;
}

.yellow {
  background-color: #ffbd44;
}

.green {
  background-color: #00ca4e;
}
